        title: "神様の手遊び",
        text: "(暗い……。ここは……森？)\n　木々が生い茂っているが、ここは円形に広場ができている。そしてそのど真ん中に彫像が立っていた。一見すると爬虫類のような、人間のような容貌が、月の光で微かに照らされて光沢が見てとれる。\n　僕は近寄って彫像に触れてみた。夜に冷やされていると思っていたのだが、ほのかに熱があるような気がする。思いのほか高さがあったので目線を上げると、彫像と目が合った。気のせいだと思いたかったが、月光が反射した黒い目は確かにこちらを見ている。\n　心臓が縮んだ。僕は彫像の背後の方向に走った。後ろで低い唸り声が聞こえる。あれは彫像じゃなかったのか。追ってくるかもしれない。僕は必死になって木々の間を抜けた。足が痛い。息が苦しい。視界が揺れる。\n　立ち止まって息を整える。どこを向いても木があるだけで方向が分からない。追いつかれたらどこへ逃げよう。まだ唸り声が聞こえる。これは、背後にいる？驚いて振り返ると、彫像が僕に向かって、前のめりに、口を開けていて。\n\n「……という夢を見たのさ」\n「はいはい、報告ありがとう」\n　友人セオンに軽くあしらわれている僕はアネムだ。最近、悪夢を続けて見るようになってからずっと、朝一番でセオンに報告している。セオンにはだいぶ飽きられているようだが、自分としてはドキドキしながらもどこか楽しみにしているので話すのが止まらない。\n「アン、俺はそろそろお前が病気か何かじゃないかと思うんだ」\n　ため息をつきながら言われる。\n「うん、僕もそう思う、けど、どこも病気じゃないっていうんだ。僕には病院は似合わないみたいだ」\n「そうか……。割と心配になってきてるんだよな」\n「どうもありがとう」\n　わざと軽く答えた。\n「あぁ、そうだアン、カウンセラー室は行ったのか？」\n「カウンセラー室？どこだそこ？」\n　聞いたことのない場所だ。学校にあったかなそんなところ。\n「知らないのか。……後で一緒に行くか」\n「あ、うん」\n　普通に教えてくれればいいのに暇なのかな、と思ったがつっこまないことにした。\n\n　放課後、僕はセオンとカウンセラー室へ行った。金色の長髪を後ろで軽くまとめた先生が優しく話しかけてきた。\n「あら、いらっしゃい？初めて見る顔ね」\n「ソーム先生こんにちは。こいつがいつも言ってる奴です」\n　思いのほかセオンが心配してくれていた。なんだか楽しんでいて悪いな。僕はソーム先生と呼ばれた人に自己紹介した。\n「僕はアネムです。セオンにはアンって呼ばれてます。よろしくお願いします」\n「よろしく。私はソムナス。ソームと呼んで。セオン君がたまに来てはあなたのことを話してくれるんだけど、私も少し気になっちゃって」\n「いやあ、まさかセオン以外にまで心配されることになっているとは……」\n　僕は苦笑した。セオンはその横でニコニコしている。\n　悪夢の話を一通りした後、先生から一枚の紙を貰った。曰く、「最近海外旅行に行ったときに買ったお守り」らしいが、太い赤ペンで再生紙の短冊に落書きしたようにしか見えない。\n「これ、お守りなんですか？」と思わず聞いたが、先生は「効果あると思うわ、有名な神社らしいし」と答えた。\n　僕は悪夢を見たくない気持ちと見たい気持ちがあったが、どっちでも変わりはしないな、と思って使ってみた。半信半疑ながら、枕の下にお守りを敷いて寝ることにした。\n\n　僕は、ここが夢の中であると自覚した。温かくも冷たい空気を感じながら、昨日の森の広場にいる。彫像は立っていない。周りを見ると、一箇所、踏み荒らされたようになっている。僕はその道を行くことにした。\n　道は途切れていたが、まだ彫像は立っていない。ここら辺で捕まったはずだ、と辺りを探していると、黒いウサギのような動物を見つけた。\n「あっかわいい」と近寄っても動物は逃げようとしない。人懐っこいようだ。\n　動物に触ろうとすると、遠くから「こら！」と怒鳴り声が聞こえた。僕は後ずさった。\n「ここで何してる！……なァ兄ちゃん、こいつが何か知ってるか？」\n　茂みから深緑色の警官服を着た精悍な男性が話しかけてきたが、僕は首を横に振ることしかできなかった。\n「……そうか。すまない、怒鳴っちまったな。俺はクヴァイってンだ。見ての通り警官だ。」\n「アネムです。……これは何なんですか」\n「あァ、こいつはな、メアっちゅうンだ。こいつが近くにいるとな、悪夢を見ちまうンだよ。だから俺たち警察が取り締まってるっちゅう訳だ」\n「……私の目ではどうしても動物捕獲が仕事のように見えてしまうのですが」\n「絵面はかわいいだろうがなァ、意外と大変なンだぜ、これ？」\n　へぇ、と生返事すると、クヴァイさんはさらに続けた。\n「夜だと保護色で目を凝らさねェと見えないし、そのクセに大事なときにメアが見つかるとこっぴどく叱られちまう。全く、だから地方は辛いンだよ」\n「大事なとき？」と首をかしげると、クヴァイさんは訝しげに僕の方を見て言った。\n「最近は夢配達とかあンだろ。折角いい夢を届けたのにメアがいたら台無しだっちゅう話だよ。……しかしお前さん、それも知らないっちゅうことは、××人だな？」\n\n　よく聞き取れなかったが、なんとなく聞き返すのはいけない気がしたので曖昧に答えた。\n「はァ～やっぱりか。じゃああまり言わねェ方がいいな、覚えてもらうと困るもんをさらっと喋っちまうかも知ンねェ」\n「そんなこともあるんですね」\n「何でも知るタイミングっちゅうのがあンだよ。それに、知らなくていいこともある」\n　クヴァイさんはそれじゃ、と言うと、メアを抱えて立ち去ろうとした。僕はその後をついていった。\n「……何でついてくンだよ」\n「いや、ただ単に興味があったので」\n「はァ……まあいい」と一息つくと、「署まで来てもらおうか」と言い、また更に間をおいて、「一回言ってみたかったンだよなァ～これ！」と満足げな表情をしていた。\n　森を抜けると、自動車が一台あった。クヴァイさんは僕に乗るよう促し、僕は後部座席に乗り込んだ。\n「乗り心地はどうだ？」\n「すごくいいですね……静かです」\n「ハッハッハ、そうだろう。まあ夢の中だっちゅうこともあると思うがな。もちろんこんなこと初めてだろ？」\n「はい。初めての人はみんなこんな経験しているんでしょうかね」\n「ンなわけねェだろ！お前さんはほんとに幸運だ」\n\n　しばらくすると、住宅街に入った。家々は現実世界でもよく見かけるようなものばかりで、思わず目が覚めてしまったと錯覚しそうだ。進む度に陽は昇り、空が明るんでいく。\n　警察署だという、白一色の大きな建物に着くころには、青い空が広がっていた。星の中心から光が放たれているマークで警察だと分かったが、文字のようなものは目を凝らしても崩れたままで読めなかった。\n　僕はクヴァイさんに引き連れられ、建物の中へと入った。いくつかの部署に分かれ、それぞれのデスクで仕事をしている。\n「……お前さん、やけに楽しそうだね」\n「警察署なんて初めて来たからさ」\n「へェ。じゃあ職場見学でもしてみるかィ」\n　クヴァイさんはちょっと待ってな、と言うと、メアを抱えて入り口から向かって右の通路へ入っていき、やがて帰ってきた。\n「ンじゃあ、まずは俺のデスクでも見てみるか？」\n　僕は勢いづけてうなずいた。クヴァイさんがよーし、それじゃあ、と動いたところで短いサイレンが鳴った。\n『警報が発令されました。警官各位配置についてください。繰り返します、警報が発令されました――』\n「悪ィな。職場見学はまたの機会にだな」\n「な、何が起こっているんですか」\n「あァ、でかいメアが出てきたんだよ。こんなことほとんどねェからな、お前さんはほんとのほんとに幸運だよ」\n　僕は乾いた笑いを出すと、クヴァイさんは、惹きつける何かがあるンだろうな、と続けた。\n\n　車に乗り、メアが出現したという場所へ向かった。\n「ここのは地方でもでかい方でな、監視カメラの映像がこっちに届くようになってンだ」\n「クヴァイさんは何でも知ってるんですね」\n「あァそうさ。……着いたぞ」\n　辺りは薄暗い。住宅街から少し離れたところのようで、木は切られ、更地になっている。そこに、象くらいの大きさのメアが佇んでいた。全身黒のそれは、まるで一枚の岩のようだ。\n「こりゃァまたでっかいなァ」\n　クヴァイさんは苦笑していた。他の警官たちは縄を持ち、捕らえるタイミングを計らっている。\n「クヴァイさんは加勢しないんですか？」\n「あァ。あんだけ人数いれば大丈夫だろう」\n　間もなく、その時が来た。メアは捕えられてもなお動かない。あっけなく終わったと思っていた。\n森の方に黒い塊が見える。\n「クヴァイさん、あれ何でしょう？」\n「……ワォ」\n　さっきと同じくらいの大きさのメアの集団。一匹であの慎重さだったと思うと、この集団を一気に捕らえることはできないだろう。\n　すると、警官たちの中から一人、こっちに向かってくる。\n「クヴァイ警視すみません！ご協力願います！」\n　クヴァイさんは短く「分かった」と言って軽く体を動かし始めた。僕は少しの間立ち尽くしていたが、その警官に、「危ないから下がっててね」と言われた。\n「クヴァイさん、今から何するんですか？」\n「あぁ、見てればそのうち分かるよ」\n「総員装備！」\n　クヴァイさんの掛け声がかかると、警官たちはどこからか透明な盾を持ってきた。こっちに来た警官も盾を持ち、皆で弧状にしゃがみ込んだ。銃撃戦の最中さながらの緊張感を感じる。\n「……クヴァイさん、大丈夫なんですか？」\n「だって警視だよ？」\n「は、はあ。僕にはお偉いさんだということしか分からないです」\n　警官はきょとんとしている。そして、我に返ったように「あっ、見てな？」と盾の先を指さした。僕もその先を見た。\n　クヴァイさんは、地面に両手をつけると、白い光に包まれた。光は大きく球形になり、やがて形を歪める。僕は唖然とした。輝きがやむとそこにいたのは白い熊のような動物だった。大きさは集団より少し背が高いくらいだろうか。\n「夢食い、見たことないのかい？」\n「え、はい。大きいですね」\n　白い動物は集団に襲い掛かった。何匹かのメアは怯んで散らばった。\n「ああいうのになれて初めて要職に就けるんだよ。僕も早くああなりたいね」\n「僕は熊は苦手です……」\n「熊？いやいや、そんなんじゃないよ、どっちかといえばバクに近いんじゃないかな」\n「ばく？」\n　白い動物は一匹のメアを両腕で掴み、丸呑みした。メアの鳴き声が小さくなり、やがて聞こえなくなる。一匹づつ、慎重に捕らえていく。いつの間にか盾が集団を囲んでおり、スタジアムのようになっていた。\n「夢食いはメアを浄化できるんだ。ある程度小さいのなら僕でも浄化できるんだけど、あれぐらいになるとどうしても、ね」\n　いろいろあるんですね、と言うと、彼は、君も他人事じゃないと思うよ、と言った。\n「僕らもメアに食べられてしまうかもしれないからね。もちろん君だって。だから、こうして守っておかないといけないんだよ」\n　僕はわざとらしくうなずいた。\n\n　十分もすると、メアの浄化はすべて終わった。けが人はいないようだ。まばらに武装が外れていく。白い動物はまた光り、人の形に戻った。「さすが警視だ」と警官は呟いた。\n　クヴァイさんはこちらへ歩いてくると、開口一番に、\n「まァ、これでお前さんの夢も食べないといけなくなったんだがな」と言った。警官も驚いている。クヴァイさんは警官に言った。\n「トルトイ、お前、気づいてないのか。こいつ××だぞ」\n「えぇ！全然、気づきませんでした……。あんまり変わんないんですね」\n「ともかく、これは覚えてもらっちゃァいけないンだよ。見ちまったンだからしょうがねェ」\n　とりあえず戻るぞ、と言って、クヴァイさんは解散命令を出した。僕は車に乗り、警察署へと戻った。\n\n　僕はクヴァイに連れられ、ある部屋に行った。文字が書かれていたが、三文字だということしか分からなかった。\n「ここは処置室だ。軽いけがの手当てをしたり、お前さんみたいなもンの処置をしてる」\n「クヴァイさん、僕はこの夢を覚えていられないんですか」\n「しょうがねェだろ。覚えてもらうと困るもんもあンだよ、ほら目を瞑れ」\n　僕はそれに従って目を閉じた。力を抜いていくと、体が浮くような感覚になり、意識が遠のく。\n「お前さんはほんとに幸運だ、ただ俺が不運を招いちまったな」\n「いい夢見ろよ、アネム」\n\n　朝がやって来た。\n「おはよう、アン。やけに不機嫌じゃないか。あのお守り、効かなかったのか？」\n「うーん……。分からない」\n「分からない？」\n「昨日の夢、覚えていないんだ」\n「悪夢じゃなかっただけいいだろ？元気出せよ」\n　あまりいい気持ちではなかった。何かが引っかかっていた。\n\n「……お守り、また使ってみようかな」\n\n　円形の広場に、彫像が立っている。暗く、光沢のある、ウサギの彫像だ。僕は、彫像の横に座って月を眺めた。彫像はふいに喋りだした。\n「ボク、ココガスキ。ナンダカ、アンシンスル」\n「僕はここで怪物に襲われちゃったんだ。でも、何でだろう、僕もここが好きだ」\n　誰かの足音が聞こえる。目の前の森から、深緑色の警官服を着た男が歩いてきた。男は、動物の彫像を僕から離して置き、僕の真横に座った。\n「よォ、いい夢見てるか？」\n「え？うん」\n　男は微笑むと、すっと立ち上がり、またどこかへ消えていった。",
      },
      {
        id: "unfinished",
        title: "未完の",
        text: "　「ドアが開きます、ご注意ください」車掌の声が聞こえる。それに合わせてドアが開くと、外気の熱風と共にスーツを来た人たちがなだれ込む。私はそれに押し流されながら、スマホをいじってネットの波を乗りこなす。吊り革につかまっている左手は全体重を支えるのに忙しい。学生の乗っているような時間で、会話と共に私の古き良き時代の記憶も掘り起こされ、哀愁さえ感じる様子を空想していたが、どうも私の近くにはいないらしく、ただただ電車の駆動音と自分の心臓の拍動が聞こえるだけだ。\n　私は、一般には地味と言われる服装で電車に乗っている。おしゃれに気を遣うほど外に出るのは慣れていないし、そもそもこうして電車に乗るとは思っていなかった。しかし、最近はそうにもいかなくなったのだ。始まりは四か月前、私がずっと引きこもっていたころだった。\n　そのとき、私は学生だった。いや、「元」学生だった。不登校が続いて学校を中退したのだ。そして、マンションの一室で、時間と食料を無為に消費していた。実家からは、今家ではどうだとか、こっちでは頑張ってるのかとか、思いつく限りのすべてを長文で書かれた母からの手紙と、たらふく食えという思いを物理的に贈ってくる父からの大量の野菜が毎月送られてきた。その段ボールを見るたびに、私は心が痛むのを感じた。\n　そしてある日、春の陽気が暖かい(多くの人にとっての)休日である。何日ぶりかにインターホンの音が鳴った。ドアを開けると、そこにはカレー入りの鍋を持って立っている、こぢんまりとした長髪の女性が困り笑いをして立っていた。見た感じ、同年代だろうか。内心、マンガでよくあるシチュエーションだなと思いながら、ドアに手をかけたままでいると、その人は早口で言った。\n「あっこんにちは！ちょうど外出してていなかったらどうしようと思っちゃいました！あっこれ奮発しすぎてめちゃくちゃ余っちゃったカレーです！どうぞ召し上がってください！いや鍋より小分けのタッパーとかの方が良かったかなでも持てないかもしれないしまあいっかこれ割と重いので注意してくださいね！あっ私今日隣に引っ越してきました藤波志乃です！よろしくお願いします！」\n　予想外の集中豪雨に気圧され、私は感謝の気持ちを述べて大量のカレーを受け取るしかなかった。そしてこれまた予想外のうちの予想外だったのだが、「何でも教えてくれそうなんで！」という理由で、彼女は私の部屋に入り浸ることになったのだ。\n\n　私と彼女は、年が近かったということもあり、すぐに打ち解けた。だが、彼女が私の部屋に来ることはあっても、私が彼女の部屋に行くことはなかった。\n　彼女は、芸術系の学校に通っているらしかった。油絵が好きというが、「いつか見せてあげる。今、大作を描いてるの、超！大作！」と言って、見せてはくれなかった。隠しているようにも思えたが、私は彼女のハツラツとした笑顔を曇らせるようなことはしたくなかったので、それきり言わなかった。\n　私に彼女を惹きつける何かがあるのだろうか。分からないが、彼女は私がひどくお気に入りのようで、簡素なインテリアがいくつか据えつけてある部屋のソファに深々と座り、私のお気に入りのインディーズを流してくつろいでいる。\n「こういう曲聴いてると創作意欲がふつふつ湧いてくるんだよね。このCD買って聴いてみたんだけど、なんかここで聴く方がいいの！」\n　来るたびに毎回紡がれるその言葉たちが私の心に充足感を与える。一方で、彼女は何か虚勢を張っているのではないかという疑問が浮かんでくる。果たして彼女は太陽なのか、はたまたそれに似せた電灯なのか。どちらにせよ、私はその光の恩恵を受けている。もう彼女なしには生きられなくなってしまったのだろうか。確かめようにも、それに白黒をつけるのが怖くて、私はのうのうと生きていた。\n　一か月ほどが経ったとき、「何か始めてみたら？」と、彼女に言われた。思えば、趣味と言えそうなものはもうやめてしまっていたし、何かを集めることもしていなかった。だが、今何かを始めたとしても、いつまで続けられるのか見当もつかない。そのことを伝えると、彼女は「趣味なんだからいつやめてもいいんだよ。ていうか、したいときにするのが趣味なんであって、こういうのは義務じゃないから」と微笑んだ。\n「あ、そうだ。カメラいいと思うよ、カメラ。目に留まったものを残しておけるし」という言葉に素直に従い、デジタルカメラを買った。ただ単に、一眼レフは私には似合わないと思っただけでデジカメを選んだが、このカメラは、すっと手になじむような気がした。\n　早速、マンションに着くまで、通り道の写真を撮っていた。撮った写真を中に入って見返すと、撮ったそのときの空気までも感じられるような気がして、すぐに気に入った。\n　それから私は、毎日見知らぬ町を歩き、その旅路をカメラに収めていった。その中でも、特に気に入ったものを印刷して、アルバムに日記とともに入れた。私の小さな旅行記は、少しずつ厚みを増していった。\n\n　およそ一週間前のことである。もはや日課になった朝の散歩から帰ってきて、写真の整理をしていると、またいつものように彼女がやってきた。雑多な話の中で彼女は、一週間ほど実家に帰省するという話をした。\n「最近、作品が仕上がる気がしなくて。リフレッシュのためにも一回帰省しようかな、って」\n　心なしかワクワクしているように見える。なんだか寂しい気持ちになったが、彼女の邪魔をしてはいけないと、やんわり後押しすることを言った。\n「あ、そうだ。彩奈も一緒に行かない？きっといい写真が撮れるよ。山も海も近場にあるし。……まあ、それくらいしかない、とも言えるけど。どうでしょう」\n　数秒、考えた。考えて、行きたい、と言った。いいのだろうか、本当に。\n　とまあ、そんな一言で、彼女についていくことが決まり、私は彼女と電車に乗っていることとなる。とうとう彼女のおかげで、ここまでできるようになったのかと思うと感慨深いが、これは夢なのではないかとも思っている。自分の意思で動いているといえども、いわゆる明晰夢のような、本当の肉体はとうに肉塊になっている状態なのかもしれない。それでも、こんな状況になっていることを素直に喜ぼう。\n　彼女の帰省先は、遠く半島の先端らしい。電車も通っておらず、終着駅からはバスが日に何回か来るという。電車の車窓から見える風景は、次第に緑が多くなっていき、目的地へ着々と近づいていることを感じられる。あれだけ高い建物があった町はもうとっくに見えなくなっていた。\n　終着駅は、誰もいなかった。まだ帰省シーズンではないにしろ、学生はもう夏休みに入っているような時期だ。「ああ、それは単純に、子どもが少ないんだよ。というか家が少ない」と、彼女はカラカラと笑った。\n　写真を撮った。光に透いた若緑から、風が通っているのも見えそうなくらいだ。……綺麗だ。恍惚としていると、バスがやってくる音がした。彼女が、微笑んでいるのが見えた。\n　乗客のいないバスに乗ると、運転席から、男性の声が聞こえた。\n「おや、志乃ちゃんやないか、久しぶりやなあ。そちらの人は？」\n「あ、田島さん。お久しぶりです。この人は石田さんっていって、私のお隣さんなんです」\n　私はお辞儀をした。おどおどしていただろうか。\n「こんなとこによその人が来るなんて、まったく久方ぶりだよ。何もないとこやけど、ゆっくりしてってな。んじゃ行こか」\n　ありがとうございます、と言って、前のほうの席に座った。ぽつぽつ、話をしながら、時々窓越しに写真を撮る。「どこ撮っても同じだよ」と運転手さんに言われたが、何か違うものがあると思って、ずっと撮っていた。\n　運賃を払ってバスを降りると、停留所の目印と、茶色い木造の待機所があった。人が三人並んで座れるだろうかというベンチを囲うように建てられており、壁にはチラシが数枚貼られている。そこで待っていると、青い車が目の前で止まり、窓が開いた。彼女の父親らしき顔が見えた。なんとなく目元が似ている気がする。\n「やあ、おかえり。そちらの方も、お話は聞いとります。さあさ、乗ってください」\n　促されるままに後部座席に乗り込むと、数分で彼女の実家に着いた。大きな家だった。驚いていると、そこまで驚くようなもんじゃないよ、と言われた。周りにも同じような大きさの家が立ち並んでいることに気付いた。マンションの一室と比べるようなものではなかったな、と思った。\n\n　そこでの一週間は、客観的には単調でありながら、有意義な時間だった。山の緑の空気を吸いながら、海の潮風を全身に浴びながら、行き交う人々のぬくもりに触れながら、写真を撮った。アルバムはすぐに厚くなった。夜には、彼女と、その両親と、ともに語らい、笑い、楽しんだ。\n　最終日の夜深く、彼女がもう寝てしまったころに、そっと彼女の両親に言った。\n　私は彼女に助けられました。彼女がいなければ、私はもう、とっくにいなくなっていたかもしれません。彼女と出会い、芸術に出会い、私は生きがいを見つけることができました。彼女には感謝してもしきれません。\n　彼女の両親は、私が言い終わるのを静かに待っていた。母親が、二冊のスケッチブックを持ってきた。\n「これね、家に置いてあったやつと、志乃が持って帰ってきたやつ。見てみなさい」\n　開いてみると、その差は明らかだった。片方は、全てのページが黒く塗られており、濃い水彩が時折ちらりと見えているだけ。もう片方は、淡い色遣いの、軽やかなタッチで描かれたページが綴じられている。私は、目を大きく開けて、息を詰まらせた。\n　母親は、優しい口調で、言った。\n「志乃もね、あなたがいなかったら、そのままだったと思うわ。外には何も出さないで、ずっと自分の中にだけ溜め込んでしまって。自分が思っていることを伝えられないままでいたかも。本当に、ありがとう」\n\n　私には、他人を変えるような力があるのだろうか。私は太陽ではない。高々が太陽の周りを回る惑星だろう。そのことを言うと、母親は笑って、\n「二つの星は、お互いが影響しあって回っているの」\nと言った。\n\n　帰りの電車の中で。私は彼女に尋ねた。\n「そういえば、前に言ってた超大作は、いつ見せてくれるの？」\n「え？ああ、あれ。」\n　一息ついて、彼女は答えた。\n「あれは、もう描くのやめた」\n「どうして？見たかったなあ」\n「あれは、ほんとは心の中のそのままを描こうとしてたの。でも、見直したら、もう心の中に残ってないような感情が描いてあって。上から描きなおしてもいいんだけど、そういう感情があったっていうことは忘れたくないな、って。だからあれは、もう完成したってことにして、私だけが見られるようにするの」\n　私は、そっか、と微笑んだ。\n\n「タイトルは、『未完の』、です」",
      },
      {
        id: "diary",
        title: "普通の日記",
        text: "　七月九日、晴れ。今日の優ちゃんは大福だった。中にぎっしりと詰まったあんこが外からうっすら見えて、とてもおいしそうな白い大福が首から上についていた。「今日は大福だね」と優ちゃんに言うと、「なんか食べたくなってきた。帰りに大福買おうよ」と言われた。\n　二限の数学の時間がとても退屈だった。専門用語がたくさんでさっぱり分かんないけど、ちょっとは分かるかもしれないギリギリのラインを攻めてくるところが、韓国語の授業を聞いているみたいだった。受けたことないけど。退屈だったときに、ちょうど前の席からあんこのいい匂いが漂ってくるから、まだ昼休みまで時間があるのにすごくお腹が空いてしまった。\n　四限の化学の時間に、こっそりつまみ食いした。一時間がまんしたのを褒めてほしい。後ろ？背中側の下ほどを少しちぎって食べると、もちもちの皮と甘いこしあんが手を取り合って私の喉を通り過ぎていった。ちぎったところが元通りになっていたので、無限に食べれらるんじゃないかと思って、優ちゃんと先生にばれないようにしながらバクバク食べた。ちぎったところは食べたら元に戻って、無限には食べられるんだけど、いつまでたっても満腹感は得られない。たぶんお腹の中に入ると消えてしまうんだろう。なんだか物足りないけど、おいしいのをいっぱい食べられたので良かった。これでもう大福は一週間はいらないな。\n　とは言いつつ、帰りに優ちゃんとコンビニに寄った。私はプリンとお茶を買った。優ちゃんは定番の白大福を買っていた。食べているところを見たかったな。\n\n　七月十日、くもり。今日は水槽だった。透明な板が黒いゴムみたいややつでつながっている。中には何も入ってない。理解にかなりの時間がいることは分かったけど、そもそも理解できるようなことじゃないので諦めた。それでも数秒フリーズしていたのか、優ちゃんが私の方に来て心配してくれた。かくかくしかじか言ったら、やっぱり訳が分からない顔をして、肩をすくめた。\n　朝は水槽に何も入ってなかったんだけど、お弁当を食べているときに少し水が入っていることに気づいた。帰るころには水槽に半分くらい水が張っていた。\n　別れ際に、中の水を触らせてもらった。ぬるかった。この水はどこから来たんだろう。ピンポイントで雨でも降ったんだろうか。もっと常識的に、科学的に考えるなら、……空気中の水蒸気が冷やされて水になった。いや、でも水槽もそんなに冷たくない。となるとこの水槽は無から有を作るオーパーツだということになる。むしろそっちの方が違和感がない。\n　昨日買った大福はおいしかったらしい。「食べてるときの写真あったらちょうだい」とダメもとで言ってみたら、やっぱり撮ってなかった。ふつう大福食べるときに自撮りなんてしないよね。というかしてほしいと思うことがおかしいんだよね。特に大福限定なところ。\n\n　七月十一日、くもりのち雨。今日も水槽だった。水は心なしか昨日より溜まっている気がした。今日はフリーズすることなく無事に優ちゃんのもとまで近づけた。「水、増えたね」と言うと、「知らないよ」と困ったような顔で笑った。\n　職員会議だかなんだかで、短縮授業だった。今日の授業はしっかり聞けた。毎日こうだったらいいのにって思うけど、やっぱりそううまくはいかないものだ。平日の授業は短くならないうえに、土曜まで学校があるなんて。巷で噂の完全週休二日にならないだろうか。まあまだ半人前の身ではあるんだけども。\n　帰るころには、水槽の中の水はもう溢れそうだった。優ちゃんが歩くのに合わせて水面が前後に移動して、こぼれるんじゃないかとハラハラしながら一緒に歩いていた。水がこぼれて滑って転ぶというのも面白いかとも思ったけど、痛いのは嫌だ。\n　玄関で靴を履き替えて、校門を出たときに、ついに水がこぼれた。バシャッ、と音がして水が落ちるのに合わせるように、夕立にあった。大きな音を立てて地面にたたきつけられる雨の中、優ちゃんは一言、「最悪だよ」と言った。\n　私も優ちゃんも傘を持っていなかったので、走ってバス停へ向かった。水槽に雨が叩きこまれて、さらに激しく動いているので、水面がぐちゃぐちゃになっている。私は運動神経が悪いので、たびたび遅れそうになりながらも、なんとか優ちゃんについていった。バス停につく頃には、もうへとへとだった。ベンチが空いていてよかった。\n\n　七月十二日、雨。優ちゃんとバスの中で会った。今日は傘だった。バスを降りてからは傘をさして歩いた。頭の傘は使わないのかと聞きたくなったけど、いくら傘になったといえども濡れたくないかと思い直した。\n　優ちゃんの写真を撮って見せると、優ちゃんは「別に普通じゃん」と言って、何事もないかのように歩き続けた。いいものが撮れたと思ったけど、これが普通なのかな？学校の中に入っても、頭の傘は開いたままだった。\n　授業中は、黒板が見づらくて苦戦していた。傘を閉じる機能はついていないように見えたので、なんとか首を動かして頑張った。\n　今日の優ちゃんは気分が優れないようで、いつもよりぼーっとしていた。\n「雨の日ってなんかだるくなっちゃって、動きたくなくなるんだよね。学校に行きたいし、頑張って起きるけど、行きたくないな、とも思っちゃう。朝は自分と戦って毎回延長戦で勝ってると思う。私が学校に行くんじゃなくて、学校が私のところに来ないかな」\n　優ちゃんの新しい一面が見られた気がした一方で、心配する気持ちもあった。みんな何かしらの悩みを持っているんだな。少しでも悩みを知って助け合えたらいいな。\n　今日はどこにも寄り道せずに帰った。やっぱり家にいると安心する。でも、そうじゃない人もいると考えると、私は幸せなんだと思う。その幸せを噛みしめつつ、早めに寝た。\n\n　七月十三日、雨。蒸し暑い。今日の優ちゃんはカエルだった。普通のカエルはよくよく観察したことはなかったけど、人間レベルの大きさだと迫力がある。首から下は人なので、カエルが制服を着ているというよりは、普通の人がカエルのマスクをつけている感じだった。そんな感じのことを優ちゃんに言うと、「あんたの普通って一体何が普通なの」と一蹴された。笑って誤魔化した。\n　少なくとも普通の人はカエルのマスクをつけて学校に来ない。それは分かる。それでもそう見えてしまうんだからしょうがない。検査しても異常はなかったし。すごい特殊能力ではあるけど、変わるのは優ちゃんの頭だけで、ほかの人は至って普通だし、世界一いらない特殊能力でギネス記録に載るんじゃなかろうか。そんな称号欲しくはないけど、貰えるというのなら貰っておこう。これを俗に、捕らぬ狸の皮算用と言います。\n　金曜日なので気分が軽い。もうすぐ夏休みなのでさらに気分が軽い。今なら空も飛べそう。期末テストも終わって、消化試合のような授業なので、空気が違う。受験シーズンには見られないことを願うばかりだ。\n　家に着いてからも、優ちゃんの言葉がずっと心に残っていた。普通って何だろう。多くの人にとっての常識？他と比べてだいたい共通するもの？なんだかしっくりこない。大多数が間違えていることだってあるだろうし。他と比べるなんてできないことだってあるだろうし。もやもやした気持ちをどうしようか考えつつ、ベッドに向かった。\n\n　七月十六日、くもり。今日は満開のアジサイだった。太陽はその威力をますます強めるばかりで、雲の盾でも暑さは太刀打ちできない。もう梅雨は明けて、夏に入ったと言ってもいいくらいのときに、アジサイの花だなんて。季節感がずれている。\n　アジサイは、土によって花の色が変わるらしい。ピンク色のアジサイは、中性かアルカリ性の土に咲く。優ちゃんの体は中性かアルカリ性なのだろう。自分で書いていてなんだか気持ち悪い。\n　優ちゃんに、「普通ってなんだろうね」と、ぽつり、聞いてみた。優ちゃんは黒板を写していた手を止めて後ろを振り向き、「あんた、まだ考えてたの？もう月曜日だよ？」と言った。\n「しょうがないじゃん、気になるんだから。他にももやもやしてることはあるけど、今はこれが一番」\n「ふうん、まあ気になるならしょうがないか。でも普通の人は『普通って何か』なんて考えないよ。哲学者みたい」\n　今日の私は哲学者のようだ。私は、自分には哲学に関連するような知識がないことを自覚している。これは矛盾？それともパラドックス？もっと勉強したほうがいいのかな。現代社会のテストはそれほど悪くはなかったはずなんだけど。理科はもっと勉強しないといけない。\n　暗い空だとアジサイはあまり映えない。太陽の光が差し込んで、そこにほんの少しの露があればいいと思う。そんな都合よくはならないだろうけど。\n\n　七月十七日、晴れ。今日はケーキだった。ふわふわのスポンジに白い生クリームを塗って、イチゴと薄い板チョコの乗った、六等分されたのであろうショートケーキ。もしかしたら私は、優ちゃんの気持ちを見ることができるのかもしれない。それが読んで字のごとく、優ちゃんが感情を顔に出しやすいのか。うまいこと言った。「今日はケーキだよ」と言うと、優ちゃんは「私がその日に食べたいものを的確に当ててくるよね」と少し不機嫌そうに言った。「優ちゃんは、感情が顔に出やすいのかな」と聞くと、「そう言われたことはある」と素っ気なく答える。\n「まあ私の目は欺けないんですけどね」と言うと、「あんたは顔にも言葉にも出やすいな」と返された。そんなに誇らしげでしたかね。自分には分からない。\n　授業の時間は、相変わらず平淡に過ぎていく。優ちゃんの頭が食べ物のときは、決まって早くお腹が空く。お腹が空いてつまみ食いしてしまう。大福とかケーキとか、甘いものだから食べる気になるんだろうけど、もしラーメンとかご飯とか、主食系だったら、私は食べる気になるのかな。食べたらなくなるから、やっぱり食べるのかな。その日になってみないと分からないな。\n　ちなみに、私がつまみ食いしていることを、優ちゃんは知っている。最初はとても不審そうに「何をしているの？」と聞かれたが、「これおいしい！」と言ったところ、これ以降は話を振られなくなった。未だに変な人と思われているのかもしれないけど、私は至って普通のポンコツ能力者だ。\n\n　七月十八日、晴れ。今日の優ちゃんはハトだった。「ハトの鳴きまねして」と言うと、抑揚も感情もない、まさに棒読みの「くるっぽー」が返ってきた。その後に流れた、無言の時間が笑いを誘って、吹き出してしまった。\n　今日は平和な日だった。何もイベントやアクシデントがない、普通の日。いや、そんな日はあんまりないから、普通じゃないのかも。ここにきて「普通」についてまた気になってしまうなんて。\n　お昼ご飯を食べたとき、ちょうどお弁当に豆が入っていた。「豆あげるよ」と言って、豆を一つずつ箸でつまんで、優ちゃんの弁当箱に入れていった。私が、途中何回か豆を落としたのを見て、優ちゃんは、「箸使いの練習でもしてるの？」と聞いた。そんなわけないじゃん。優ちゃんは、普通に豆を食べた。想像していたよりはるかに面白みがなかった。自分の想像力がすごいんだと思うことにした。\n　今日はゆったり時間が過ぎた感じがした。四限の現代文の時間が自習になったので、眠気を必死にこらえながら、夏休みの課題を進めた。午前は乗り切ったといっても、お弁当を食べた後は、すごく眠くなってしまう。寝てしまったら、優ちゃんにクチバシでたたき起こしてもらいたかったけど、そんなことはなく、手で優しく肩をたたいて起こしてくれた。\n　帰りも、いつも通りに、優ちゃんとしゃべりながら帰った。もうすぐ、こんな時間が一か月ほどなくなるのは残念だけど、一か月後が楽しみだ。\n\n　七月十九日、晴れ。今日はひまわりだった。太陽に負けないくらいの大きなひまわりは、特に太陽の方へ向いているわけではなかった。優ちゃんと話すたびにこっちへ向く花は迫力がある。そして同時に、もう夏になったんだなと感じさせられる。\n　今日で一学期の授業がすべて終わった。夏休み中でも補習はあるけど考えたくないね。夏休みなんだから、普通休むものでしょ。それとも、休まないのが普通なの？「普通」って理不尽だ。そうでも思わないと夏休みの存在が消えてしまいそうだ。\n　七限のホームルームは、自由時間だった。多数決で、トランプをすることになった。四人でグループになって、メンバーを変えながら、なぜか準備してあったトランプで大富豪をした。\n　優ちゃんは、圧倒的な力を見せつけて優勝した。無敗だった。優ちゃんは「運が良かっただけだよ」と気分よさげに言っていたが、なにかものすごい計算をしていたんじゃないだろうか。今日は、優ちゃんから尋常ではないパワーを感じ取った。帰りに、優ちゃんとコンビニに寄って、アイスを二つ買った。ちょっとした贅沢だ。\n\n　七月二十日、くもり。終業式の日。今日は久しぶりに、優ちゃんの顔を見た気がする。切れ長の目と、肩まで伸びた茶髪が最高にクールです。「今日はちゃんと人間だよ」と言うと、「まるでいつも人間じゃないみたいじゃないか」と即答された。私から見れば、頭は人間じゃない状態が普通なんだけど。\n「だから、あんたの普通って何が普通なんだよ」と聞かれたので、私はちょっとの間考えて、「なんとなく分かるけど、なんとなく分からないもの」と言った。いいこと言った気がする。優ちゃんは、「すごいドヤ顔してるけど、私にはさっぱり分からん」と笑い交じりに言った。\n　式が終わってからは、さらにさわやかな気持ちになって、優ちゃんを急かして走って校門を出た。おそらく、その日校内で一番早く学校を出ただろう。残念ながら空には薄く雲がかかっていたけど、気分的には雲一つない青空の下にいた。走り疲れて立ち止まったとき、優ちゃんは息をきらしながらも、「楽しいな」と笑った。昨日のひまわりよりも輝いていた。\n\n　これで、少しの間、日記を書くのを休もうと思う。また習慣づけられるかは分からないけど、今日までの日記には満足している。また学校で会うときは、優ちゃんは何になっているんだろう。今から楽しみで仕方がない。",
      },
      {
        id: "liar",
        title: "噓つきの魔女と黒猫",
        text: "　ハロウィーンの日だった。街は仮装をした人たちでにぎわい、いつにも増して活気にあふれている。私も例に漏れず、数人の友達と仮装をして街を練り歩いていた。憧れの魔女の仮装に身を包み、私が私でなくなっているようだった。\n　両目が隠れるくらいぶかぶかで、先の折れ曲がったとんがり帽子に、これまたサイズの大きいローブ。師匠に憧れた見習いの少女ともとれる姿だが、手に持っているごつごつとした杖が高貴さを物語っている。\n　街はオレンジや紫で彩られており、うまく雰囲気に溶け込めているようだ。一日限りで立っている屋台にも、ジャック・オー・ランタンをかたどったカゴが吊り下げられ、中にはチョコレートや飴が入っている。私たちはそこで買い物をしたり、時には小さい子にお菓子をあげたりして楽しんでいた。\n\n　夢と現実との狭間のような時間は、すぐに終わってしまう。日の光が赤く変わってきたころ、人混みはしだいに少なくなり始めた。もうこんな時間になってしまったのかと、驚きと寂しさを感じながら、私たちは解散した。店じまいの最中に吹く冷たい風が、冬を予感させる。\n　私は、大きな買い物袋を結びつけた杖をつきながら、夕日の方へ向かっていた。大通りから路地に曲がるときに、前の方で声がした。なかなか渋い声だった。\n「そこのお嬢ちゃん、ちょっといいかい」\n　立ち止まって目を凝らしても、建物に遮られて薄暗い道には人がいない。聞こえなかった振りをして、また歩き出すと、また聞こえた。\n「お嬢ちゃん、まさか見えてないのかい？」\n　見回しても、やはり誰もいない。コンクリート造りの建物と、民家と、塀があり、そこに黒猫がちんまり座っているくらいだ。\n「こんなに堂々といるのに、まさか見えてないなんてことはないだろう、なあ」\n　その声は、黒猫の辺りからするように聞こえた。\n「――猫ちゃん？」\n　気が抜けてしまった声に、凛とした声で返事がくる。\n「まだお祭り気分か？――まあいいが」\n　黒猫は一呼吸おいた。\n「ちょっとついてきてくれ。わけは後で話す」\n　実際には初めて聞くような台詞に困惑と背徳感を抱きながら、私は黙って首を縦に振った。それを見ると、黒猫は微笑して、前に向き直った。私の方を振り返ることなく、すたすたと歩いていく。呆気にとられていた私は、急いで黒猫の後をついていった。\n\n　細い路地を歩く。大通りとは対照的な、閑散とした住宅街。風が鳴る音、木の葉が擦れる音、道を踏む音が、そのまま耳に伝わってくる。\n　黒猫は路地をすいすい歩く。私が通れなさそうな道を避けて、迂回している風にも見えるし、わざと曲がる回数を増やして、道を覚えられないようにしている風にも見える。生憎記憶力はそこまで高い方ではないので、早々に道筋を覚えるのは諦めた。\n\n　いくらか経ったとき、曲がるたびに重々しい雰囲気を感じ始めた。黒猫が歩を止めたのは、神社の前だった。人の話し声が聞こえる。それも大勢だ。こんなところに、どうして人が集まっているのだろうか。入口の横の石柱に、神社の名前が彫られていたようだが、相当古いのか、壊れていて読めなかった。\n　私はこの神社に来た覚えがない。そもそも、近くにこんな神社があるとは思っていなかった。黒猫はこちらを一瞥し、鳥居をくぐっていった。私もそれに倣って、境内に入った。\n　本殿の周りに、黒い服をまとった人たちと、さまざまな種類の猫がいた。井戸端会議を楽しそうにしている者もいれば、階段に座ってくつろいでいる者もいる。明らかに日常とは違う、異様な光景を目の当たりにした。精神が削れる様子を体現するように、心臓が萎縮する。ここに来てはいけなかったのだろうか、しかし、もう遅いということも悟った。\n　丁度いいことに、魔女の仮装をしていたので、自分だけ目立つといったことはなさそうだ。もっとも、周りの人が皆仮装をしているとは思えないのだが。\n\n　そう考えているうちに、前の方で大きな声が聞こえた。三毛猫だった。\n「やあクロ、ずいぶんと遅かったじゃないか」\n「主役は遅れてやってくるもんさ」\n「いつから主役になったんだい？」\n「生まれた時からさ。僕の人生の主役はずっと僕だ」\n　クロと呼ばれた黒猫は軽快な口ぶりでおどけたが、三毛猫の方は困った様子で口を結び、小さく「六十点」と呟いた。黒猫は不服そうだ。\n　一息ついて、三毛猫が話を切り出す。\n「その後ろの人がクロの主人かい？」\n　しれっと、黒猫が肯く。三毛猫はこちらをちらりと見ると、「そうかい、そうかい」と何かを噛みしめるように笑った。黒猫はそれに構わず話す。\n「どうだ、言っただろう。まだ魔女はいるんだって」\n「へえ、こりゃあ参った。まさか本当に見つけてくるなんてな。どこにいたんだい？」\n「商店街の大通りさ。一般人のお祭り騒ぎに紛れてた」\n　二匹の猫は、私を置いてけぼりにして話を進めている。とりあえず今の状況を聞きたいが、肝心の黒猫は喋っているし、他に訊けるような人はいない。まごまごしていると、不意に声をかけられた。振り向くと、セレブのパーティーにいるような、決して、さびれた神社には釣り合わない、赤いワンピースと黒のショールを着た女性がいた。\n「あら、お困りの様子で。黒猫のご主人かしら」\n「ええ、そうなんです。ここに来るのは初めてで――」\n「そうなの。ずいぶん不親切な猫なのね。それなら私が教えてあげるわ」\n　彼女はそう言うと、優しく語りかけるように、ゆっくりと話し始めた。\n\n　時代は数十年前にさかのぼる。日本でのハロウィーンは、とあるテーマパークでイベントが行われたことが始まりだ。そこから、ハロウィーンの認識が世間に浸透していった。\n　魔女たちは、その身分を隠して生きていた。私たちは魔女の一族だと代々伝えられていったが、それを実感できる要素がなかったのだ。しかし、魔女の存在が浸透したことで、かろうじてハロウィーンの日だけは、本来の魔女の服装をして外に出ることで、実感できるようになったという。\nそこで、この日に、魔女たちで秘密裏に集会を開き、交流しようと始められたのだ。この集会は、同士を探し、結託するのも目的のようである。\n\n「というわけなの」と締めくくり、彼女は一息ついた。\n「なるほど。それならどうして、猫がついてきてるんですか？」\n「猫は魔女の補佐役なの。それに、猫同士のつながりも強くてね。情報がすぐ回ってくる」\n　彼女は最後に、「それでも、新しい魔女が見つかるなんて十年ぶりくらいね」と付け加えた。何かを悟っているかのように微笑んでいる。そうしているうちに、黒猫と三毛猫がこちらへ歩いてきた。\n「あら、ご主人。いつの間にお知り合いに」と三毛猫が言う。\n「ええ、ついさっきね。クロさん、あなた、この子になんにも教えてないの？」\n　黒猫はうつむき加減に「はい」と呟いた。\n「そういうところはちゃんとしなさいよ」と戒めると、彼女は私の方に向いて、「それじゃあ、私たちはこの辺で」と言って去っていった。\n\n　黒猫と二人きりになったところで、黒猫が苦々しく口を開く。\n「――それで、大まかな話はあの人から聞いたんだろう」\n「そうね。だいぶ話してもらった。あなたからは何かあるの？」\n「そうだな。――僕はあんたが魔女じゃないって知っている」\n　私はただうなずいた。\n「それでだ、今日だけでいい。魔女のふりをしていてくれないか」\n　黒猫は、どうかこの通り、と伸びながら座った。\n「これからあくびでもするの？」と訊くと、黒猫はムッとして「精一杯の土下座だ」と答えた。\n\n　日はもうそろそろ姿を隠しきる。まだ空に光はあるといえど、もう夜だ。帰ろうとしても、道は覚えていない。\n　その後も何人かと駄弁りあい、一人佇んだりして、時間は過ぎた。\n　黒猫が、「どうだい、もうそろそろ慣れてきただろう」と話しかけてきた。\n「ずいぶんね。自分でも驚いてる」\n「それが僕たちの常識さ。あんたの常識は変えられたかい」\n「とっくに変わってるよ。そうでもしないと今頃怖くて失神してるね」\n「冗談もうまくなってきたもんだ」\n「点数をつけるなら何点ぐらいかな？」\n「そうだな、――五十点だな」\n「あなたより低いの？心外だね」\n「弟子にでもしようか。眷属兼師匠というのも面白いだろう？」\n「遠慮しとくわ。むしろ私が師匠になってもいいけど」\n\n　そんな話をしているうちに、一匹の猫が近づいてきた。夜の猫は、体のほとんどがもう見えなくなっている。\n「おい、クロ。聞いたぞ、新しい魔女を見つけたんだってな」\n「ああ、そうさ。どうかしたか？」\n「いやいや、ちょっと気になってね。それは本当に魔女なのか？って」\n「そうに決まってるだろ。見て分からないのか？」\n「へえ、見ただけで分かるなんて、お前の目はすごいなあ」\n「もっと褒めてくれていいんだぞ」\n　猫は鼻で笑うと、私の方を向いた。\n「俺はすごいから分かるけどな、あんたには魔力がない。残念ながら、嘘つきはここにいる資格なんてないぞ」\n「へえ、見ただけで分かるなんて、お前の目はすごいなあ」\n「もっと褒めてくれていいんだぞ」\n　私は、何も言えないで立っていた。言い合う二匹の猫をずっと見ていた。\n「――クロ、帰ろう」\n「あ？あんたが正真正銘の魔女だって分からせないといけないんだよ」\n「そんなの後だってできるでしょ。今日はもうおいとましよう」\n「後にはできないんだ」\n「いいから」と言いながら、私は黒猫を両手で掴んだ。黒猫はじたばたと暴れているが、逃げられることはない。\n「それでは、またの機会に」と言って、両手で黒猫を持ったまま鳥居をくぐり抜けた。\n\n　帰り道は、また黒猫の先導だ。街灯の下を心持ち急いて歩く。途中で、ぽつりと、黒猫が呟いた。\n「今日はありがとな」\n「どういたしまして。最後の猫には多分ばれてるけど」\n「一匹くらいならいいさ。多少弁が立つのが厄介だが」\n「それであなたは大丈夫なの？」\n「分からない。来年集まるころには、噂が皆に広まって、最悪追い出される」\n「――そう。ごめんなさい」\n「いいさ、いいさ。それに、僕を飼ってくれるんだろう？」\n　えっ、と声が漏れる。\n「そりゃあ、帰ろうって言ったら、家に帰るよなあ。それに僕がついて帰るとなったら、僕はあんたの家に行くよなあ。」\n　黒猫は少しためて、大声で「そりゃあ、僕はそこに居座るしかないよなあ」と言った。なんて傲慢なやつだ。\n「しょうがないなあ」と言うと、黒猫は口を横に大きく広げ、にんまりと笑った。\n　その日は、黒猫を持ち帰り、なつかれてしまったから飼いたいと説得した。両親は渋々了承してくれ、私が世話役になった。もちろんペット用品は何もなかったので、明日買いに行くことにして、今日は私の部屋で一緒に寝た。\n　説得できて良かった、と言うと、黒猫は、ありがとな、と答えた。\n\n　次の日の朝、十一月の始まり。日の光が暖かく照る。起きて伸びをすると、それに合わせて黒猫が鳴く。\n「おはよう、クロ」\n　にゃーん、と返事が来る。――それだけだ。\n「あれ？どうしたの？」\n　にゃん。黒猫は優雅に歩き出す。まるで、人語を喋らないことが常識であるかのように。そこで、たった一日だけで、猫が喋るのが当たり前に変わっていたことに気づいた。夢のようではあるが、昨日の出来事が現実であることは、目の前にいる黒猫が証明している。ふっと、息だけの笑いが出た。\n「そうだったなあ」と独り言を呟く。\n「常識に囚われてちゃあいけないな」\n　にゃーん、と黒猫が鳴く。また来年のハロウィーンの日に、突然喋ったりしてくれないだろうか。そんなことを思いながら、私は日常へと戻っていった。",
      },
      {
        id: "aulait",
        title: "いちごオレ",
        text: "　僕はまだ青春の味を知らない。\n　青春、なんて言葉は、当の本人たちには全くピンとこないもので、僕はまだ、これが青春か、と思ったことはない。それでも、大人になって振り返ってみると、今のこの瞬間が青春なんだろうな、とふと感じる。\n\n　いちごオレを飲み始めたのは、あるCMを見てからだ。といっても、地上波で見るようなちゃんとした作りのものではない。文化祭の劇紹介の映像で流れたCM。劇の内容は一切なく、ただいちごオレを勧めるだけのものだった。実際、劇の内容は覚えていないのだが、一回しか見ていないCMの方はなぜかうっすら覚えている。\n　食堂横の自販機で初めていちごオレを買って飲んだとき、なんとも平凡な味だと思った。酸味のないイチゴを牛乳に溶け込ませたような味で、後に残るその甘い香りがクセになる。それから、我ながら飽きもせず、時折いちごオレを買っている。\n　いつごろからだったか、紙パックの柄が変わり、上部に「青春の味」と銘打たれ、側面には学生の絵が描かれるようになった。\n　一度、気分が落ち込んだときに飲んでいると、ふと淡い色の学ランとセーラー服の絵が目に留まった。説明するまでもなく今はブレザーなものだから、なんとなく中学生のような気がして、僕が中学生のときの思い出が全部、すでに色褪せているように思えたので、それから落ち込んでいるときに飲むのはやめた。\n　青春を知らないまま機会を逃すのも惜しいが、何をすればいいのかも分からない。ある人からみればこれらは記憶に留まらない無駄な一日になるのだろう。\n\n　昼休みになると、僕はいちごオレを買いに一階へ降りる。僕の中では、階段を往復する足労よりも、それを飲む楽しみの方が勝っている。それか、すでに習慣づいていて苦になっていないかだ。\n　そして、また教室へ戻り、友人――岬尋紀――の席の向かいでお昼を食べる。教室の中のざわめきは、それぞれのグループの会話の内容をうまく隠していて、何かBGMが流れている中で喋る心地がしている。\n「……お前ってよくいちごオレ飲んでるよな」\n「そうだねえ」\n　他愛無い言葉で会話が始まる。\n「俺、ずっと思ってたんだけど、青春の味っておいしいのか？」\n「これはおいしいよ」\n「……ああ、そうかい」\n　尋紀は言葉が出ないようだった。\n「青春ってなんだろうね」\n「さあな。やっぱり恋とかなんじゃないか」\n「恋か……」\n　残念ながら、僕は恋愛関係には鈍感だ。もしかしたらこの学校にはカップルなど存在しない可能性もないわけではないが、僕が知っている限りはいない。つまりそういうことだ。数瞬の思考のうちに、やはり尋紀はそれを見抜く。\n「まあお前はそういうのは無縁だろうな」\n「僕もそう思ってた」\n　僕は軽く笑ったあと、一息ついて尋ねた。\n「恋愛ってした方がいいのかなあ」\n　尋紀は吹き出しかけたのを抑えて、なんとか顔を元に戻した。\n「いや、すまん。お前からそんな言葉が出るとは思わなくてだな」\n　そう言ったあと、まあ、と一つ付け加えてから、\n「しようったって簡単にできるもんじゃないだろ。まあ片思いくらいなら簡単にできるだろうがな」\nと答えた。\n「なんかそれすごい青春」\n「だいぶ語彙力落ちてるぞ」\n　危ない危ない。頭が働いていなかった。\n　尋紀は続けて言う。\n「ほら、最近LGBTだのマイノリティだの言われてるだろ？そういうのではない？」\n「うーん、よく分かんない」\n　分かんない。\n「俺も分からんけど、まあ、そういうのかもしれないとちょっと思っただけだ」\n「確かにね」\n　そう言って、いちごオレを一口飲んだ。一瞬喉が詰まって、また開く。思考は元に戻りそうにない。\n\n　放課後、教室。冷たい空気の中にいる。なんとなく並んでいる机と対照的に、黒板はきれいな深緑色だ。そろそろ時期が時期なので、ある一種の寂寥を感じながら、ワークとノートを開く。\n　外の天気は心情描写というわけではないが、明るめの雲が空を覆っている。今日は傘を持ってこなかったなあ。\n　黒鉛が削れる音。紙が曲がる音。心臓が動く音。空気が唸る音。\n　頭がフル稼働する。といっても、目の前にある課題に集中しているわけではなく、副次的に考え事が進んでいるだけなのだけれど。\n　自分がどんな人間かなんてどうでもよくて、僕はただ人間であるだけで生きていかなければいけないし、普遍的な人間が成し遂げるべきことをしなければならないという社会的な暗黙の了解は嫌になる。僕には他人の人間性なんか分からないし、相対的な評価としての僕の人間性も分かりはしないな、と感じた。そう感じるのに秒単位の時間は必要なく、これが僕の常識だと思い込みたい。\n　皆は誰かを恋愛的に好きになったことがあるのだろうか。僕には恋愛的というのが分からないから、誰かに説明してもらいたいのだけれど、普通を自称している優しい人はいるだろうか。多数派がいるということだけが分かる中で、あまりに多くの専門的な用語が飛び交っても、僕には処理しきれない。ただ、普通とは少しずれているのかもしれないと、そう思った。\n　それでも、折角だから、人生は謳歌しようと思っているが、今までの自分はどれもこれも中途半端だということは分かっていて、背伸びしたセンスも、したくもない努力も、必ず要るであろう向上心も程々にしか持ち合わせていないのが些か不憫だ。きっと、今こんなことをしようとも、確かな答えを得たり、解決することはできなくて、一生知らないまま死んでいくのだろう。さすがに世界のすべてを理解することはできないだろうが、身近にある青春というものさえ分からないまま人生を締めるのは勿体ない気がする。\n　せめて概念だけでも恋愛を体験したいが、誰に頼めばできるのだろうか、全く見当もつかない。顔が広いわけではないし、数少ない知り合いに言っても十中八九変な顔をされるだろう。\n　かろうじてほんの少しの希望を見出せるのは尋紀くらいか。明日にでも聞いてみるかな。やめておこうかな。\n　日が落ちる。肌寒くなるのを感じる。それは一体なんのためか、僕はシャーペンを回しながら黒板の一点を見つめた。空回りする音が聞こえる。僕は今、何も考えてはいない。ただ頭にこもった力を感じているだけである。\n　僕が一体どういう人間か分からない。今生きている意味なんて誰も分かっちゃいないだろうが、そういう類の漠然とした不安が襲う。いくら考えても、はずだ、だろう、と推量するしかないのが一層気持ち悪い。\n　下校時間の放送が鳴った。今日はほとんど進まなかった。シャーペンやらノートやらを、きれいに鞄に収める。辺りはいつの間にか真っ暗で、教室の電灯が際立って光っていた。どうやら外は雨が降っているようで、今日は傘を持ってこなかったなあ、と思った。それより他はなかった。\n\n　次の日。朝から雨が降っていたので、傘を差してきた。いくら外が寒かろうが、教室に入ると人の熱で少々暑いくらいにはなる。なんとなく頭が回らない気がするのも、きっと湧き上がる暑さのせいだ。\n　昼休み、僕は小銭を手に階段を下りた。食堂は毎日のように待機列ができていて、それを横切って自販機の前に立つ。百円、十円を入れて、ボタンを押す。間違えてプリンを選ばないように注意。ちなみに一回やってしまった。アームがいちごオレを運んでくる間に、ストローを一本取り、ガタンと音がしたら、受取口を開けて取り出す。少し膨れた紙パックと、中で揺れる液体が、満足感を増幅させる。\n　教室に戻り、いつもの席に着く。尋紀がなんだかにやついていた。\n「で、昨日のアレはなんか進んだのか？」\n「ん？ああ、そうそう。付き合ってくれない？」\n　これには耐えられなかったらしく、尋紀は盛大に吹き出して笑った。\n「お前、あれからどう飛んだらそんな思考になるんだよ」\n「いや、だって、頼めそうなのが尋紀しかいなかったから」\n「もっと友達作ろうな」\n　正論に立ち向かえるほどの余裕はなかったので、素直にうなずいた。\n「それで、どう？一日だけでもいいから」\n「どういうことをすればいいのか分からんな」\n　曖昧な返事。まあ、雰囲気があるときだけでも体験できたらいいかと楽観的に考えた。\n　いちごオレは、今日もほんのり甘い。きっと相手のことを想う時間がこんな味なんだろう。\n\n　放課後、尋紀に、今日は一緒に帰らないか、と聞いた。いつもは部活があって帰るのが遅いのだが、どうやら今日はないらしい。雨が降っているからだろうか。\n　尋紀はすぐに、いいよ、と言った。二人で傘を差して校門を出る。空は大分暗いが、日が沈むころほどではない。\n「こういうときって、相合傘とかするんじゃない？」\nと、冗談交じりに言ってみると、尋紀は笑って、\n「さすがにここらはまずいから、ちょっと歩いてからだな」\nと言った。制服を見かけなくなった辺りで、僕はそっと傘を閉じ、尋紀の傘の中へ入った。\n「……なあ、これ、すごい肩が濡れるな」\n「もうちょっと近づけばいいんじゃない」\n「それもそうか」\n　雨が傘に当たる音。車が走る音。線路が鳴る音。束の間の沈黙。\n　なんだか気まずくなると思って、何も考えずに言葉を発した。\n「いやあ、昨日はずっとこんなこと考えてて」\n「え？すごい時間かけてるな」\n「なんか止まらなくって。放課後もこれに費やした」\n「そこまで悩むことか」\nと、尋紀は呆れ混じりに息を吐いた。\n「よく分かんないから全然考えが進まないし、堂々巡りしてる気がするし、課題は手につかないし。もう半分ぼーっとしてただけ、みたいな」\n　尋紀はくつくつと笑う。\n「それ、もしかして風邪なんじゃないか？」\n「あー、あるかもしれない」\n「早く帰って寝ろよ」\n　僕の気の抜けた返事で、一旦会話が止まった。\n「まあ、俺はな、そうやって一丁前に悩むのも青春だと思うよ」\n　何だそれ、と思わず笑ってしまった。\n「随分と格好つけた台詞回しじゃないですかあ、岬さん？」\nと茶化すと、尋紀は顔を背けながら、うっせー、と小さな声で言った。なんとなく、顔が赤い気がした。",
      },
      {
        id: "ending",
        title: "終末の春",
        text: "誰かが明日で世界が終わると言った\nくだらない作り話だ\nテレビの電源を切った\nみんなは口には出していなかったけれど\nどうやら知ってたみたい\n無意識下のミーム汚染\n\n終末の春を心待ちにしてるんだ\n何が嬉しいんだろうか\n生きるのが辛いんだろうか\n庭には桜が見ごろを迎えている\n何も起こらないはずさ\n知らないままでいいから\n\nそのままお休み\n目覚めないでね\n三週間だけ待っててね\n終わりは始まり\nきっとどこかで\n新しい明日が待っている\n\n澄んだ空から降る光で目が覚めた\n目覚まし時計を叩いた\n今日でみんな死んじゃうんだ\nさえずりが春風に乗って聞こえてくる\nやり残したことはないか\nあってもどうしようもないや\n\n力を込めてテレビの電源を入れた\nどこのチャンネルを見ても\n同じことだけ言ってるんだ\nお偉いさんが畏まってこう告げた\n一か月後に世界が終わる\n額縁を掲げながら",
      },
      {
        id: "animal",
        title: "動物と彼",
        text: "　彼は不思議な人だ。\n　僕が彼に出会ったのは、中学生のときだ。一年生で同じクラスになり、部活も帰る方向も丁度同じだったので自然と話すようになっていた。\n　彼には不思議な力があった。彼の周りには動物がたくさん集まってくるのだ。僕が彼と一緒に登校していると、ブロック塀を乗り越えて猫がやってくるし、田んぼからは雀がやってくるし、何なら竹垣の隙間から蛇までもがやってくる。\n　それらの動物が集まってくるのを、彼は一匹一匹に挨拶する。彼なりのルールがあるようで、餌は絶対に与えない。しかし動物たちは、僕でも感じるほどの彼の温かさに引き寄せられているようだった。\n　そんな彼が、最近息を引き取った。僕は地元を離れていたが、その訃報を聞いて、唯一と言ってもいいほどの親友だからと有給を取って実家に帰った。故郷は心なしか緑が痩せ衰えて、黄土色や茶色がちらほら見えた。\n　葬儀場も例に漏れず、随分と汚れて古臭くなっていた。しかし、中は綺麗に清掃されており、ここだけ世界が違うような気がした。\n　黒や白の服を身にまとった人達が大勢葬儀場に集まり、通夜が営まれた。昔の同級生の面影が残る人は数人いる。おそらく実際はもっと多いだろうが、僕の交友関係が狭いからよく分からない。\n　通夜の後、僕は親族控室に顔を出した。彼の母は、僕を見るとすぐにこちらに近寄ってきた。長らく、彼女と会話を交わした。\n「あの子、動物好きだったでしょう。亡くなる前に私に言ったのよ。僕の棺の中に、動物のぬいぐるみを入れてほしい、僕が生きている動物の命を奪うのは忍びないから、って」\n　僕は、彼らしい死に方だ、と思った。\n　やはり、彼は不思議な人だ。",
      },
      {
        id: "abitof",
        title: "ひとくちいかがですか",
        text: "　◆まえがき\n　私は普段あまり本を読みません。自分の中で読書より優先順位が高いものが多いというのが大きな理由ですが、漫画と比べると、読むときに必要な精神力が大きいというのも一つです。漫画だと見れば多少の情報が入ってきますが、活字は読むという行為が必要なので、本に注意を向けるとだんだん疲れてきて途中で諦めることがしばしば。\n　そういう人のために、原稿用紙一枚分の超短いストーリーをいくつかまとめました。本当は数ページほどのものを書きたく、構想を練っていたのですが、プロットを書き終えた時点で燃え尽きました。残念。\n　それはそれとして、その超短編を合わせて七つ載せました。それぞれ別のベクトルのお話になっているはずなので、一つずつお楽しみいただけるかと思います。良ければ、ひとくちいかがですか？\n\n\n　◆彼と動物\n　親しくしていた彼が、最近息を引き取った。僕は地元を離れていたが、その訃報を聞いて、唯一と言ってもいいほどの親友だからと有給を取って実家に帰った。故郷は心なしか緑が痩せ衰えて、黄土色や茶色がちらほら見えた。\n　彼には不思議な力があった。彼の周りには動物がたくさん集まってくるのだ。猫やら犬やら雀やらが集まってくるのを、彼は一匹一匹に挨拶する。彼の温かさに引き寄せられているようだった。\n　通夜の後、僕は親族控室に顔を出した。彼の母は、僕を見るとすぐにこちらに近寄ってきた。長らく、彼女と会話を交わした。そのとき、彼女は僕にこう言った。\n「あの子、動物好きだったでしょう。亡くなる前に私に言ったのよ。僕の棺の中に、動物のぬいぐるみを入れてほしい、僕が生きている動物の命を奪うのは忍びないから、って」\n　僕は、彼らしい死に方だ、と思った。\n\n\n　◆辞書\n　隣の席の磯貝君が唸っている。この前あった講演会の感想文が書けていないので居残りしているのだ。不規則に漏れる唸りが妙に気に障る。ワークが進まないので、ちょっと静かにしてくれない？、と気持ち優しめに言うと、磯貝君は、はっとして謝った。\n　そんなに感想文書くの悩むの、と聞くと、磯貝君は感想がないと言った。感想なんて何々がすごいと思っただとか、良かっただとか、それと考察ぐらい書いておけば自然と埋まるものだと思っていたが、そもそも何も思わないのだとかいう。感情ぐらいあるだろうと思うけど。なんか探してみたら？と言うと、磯貝君はまた同じように唸った。それからは静かだった。\n　次の日、磯貝君は机の上に辞書を置いていた。感情類語辞典。行動が早いな。ページをめくりながら時折、これが、と声が聞こえてくる。学習機能が作用しだしたアンドロイドみたいなことを言うな。感想文は無事に終わったらしい。この調子で感情を知覚していってほしい。\n\n\n　◆死神\n　あれは中三の九月、金曜日の話だったか、朝登校したら丁度後輩と出くわしたんですよ。その人とは小学校が同じでよく喋ってたんだけど、中学に入ってからは全然会う機会がなかったんですよね。それはそれでいいとして、その人がその日に限ってか分からないけど、いやにちょっかいをかけてきたんです。何を話せばいいのか分からなくてそういう形になっちゃったのかな、とは思うんですけど、まあ正直ちょっと鬱陶しかったですね。\n　その次の日、その子が亡くなったんですよ。ええ、突然に。部活の大会で移動してたら事故に遭ったみたいです。それでそのとき思ったんですよ。もしかしたらその人は自分が死ぬっていうことを知っていたのかもしれない。僕に何かを伝えたかったのかもしれないと思うと、僕は何かできたんじゃないかな、と思うんですよね。未だに。\n　そのときから、死神はいるんじゃないかなと思い始めました。\n\n\n　◆明朝体\n　マグネシウムの力で洗濯物の汚れを落とす、と説明書には書いてある。「還元水ですので、繊維自体に付着したニオイや汚れの成分を元の状態に戻そう(還元)という自然のチカラを持っています」という説明書きが胡散臭さを引き立てる。\n　それに加えて、明朝体のフォントがより一層それを感じさせる。偏見ではあるが、効果があまりない商品の謳い文句はだいたい明朝体で書かれている気がする。きっと私みたいな印象論で語るような人がそれを買っているんだろうなと考えているが、私はそうではないと思いたい。これは効果があると思いたい。明朝体から疑似科学の匂いを感じ取っているのだろうか、そんなものあるはずはないので、きっとこの鼻は飾りなのだろう。\n　化学の知識が乏しいので、本当に効果があるのかと思って調べてみると、正しい使い方をすれば効果があるらしい。説明文やデザインを変えたら少なくとも私の信用度は上がるのになあ。\n\n\n　◆昇華する\n　高校生になったものの、未だにしょっちゅう涙が出てしまう。少しでも嬉しかったり悲しかったりすると、泣きたくなくても泣いてしまう。私は、この涙をなんとか有効活用できないかと、蓋つきの容器に涙を溜めておくことにした。水彩画を描くときにでも使えるだろうか。\n　ある程度溜まったあるとき、教室で新作のデザイン案を考えていると、近くを通りかかった男子が言った。「藤村さんって、こんなぐちゃぐちゃっぽいのも描くんやね」\n　そのときは、考えてる途中だから、と答えた。涙は出てこなかったのに、それを振り返ると、言い訳のように聞こえて、自然と涙が出てきた。\n　今が使い時だ。赤色の絵の具を溜めた涙で溶かして、白いスケッチブックに描き殴った。途中、視界がにじんできたけれど、描き進めた。\n　描き終えたそれを改めて見ると、何を描いたのか分からなかった。ひどいなあ。また涙を溜めよう。\n\n\n　◆評論文\n　評論文の問題が解けない。ちゃんと読めば分かるはずなのに、分からない。もっと簡単な言葉で評論文を書いてほしいと思ったことはないだろうか。私はある。\n　なぜ評論文が比較的難しい言葉で書かれているのだろうか。それは、評論文の意義に関連する。\n　そもそも評論文は、筆者が自分の意見を主張するための文章だ。そのために、筆者は言葉と論理を使う。この言葉というのが厄介なものだ。\n　言葉は、完全なものではない。筆者の思考と、それを伝えるための言葉には、微小な誤差が確実に存在する。その誤差をなくそうとすると、普段使わないような難解な単語を組み込まざるを得なくなるのだ。主張を大雑把に理解してほしいのであれば、簡単な言葉に置き換えて伝えることは不可能ではないが、その分だけ主張の本質との誤差は大きくなる。それがあまりにも大きいと、本来の主張とは別のことを主張しているともとられかねない。\n　正確な主張のために、難解な言葉の評論文がある。\n\n\n　◆吸い込む網\n　夏、公園の通りを歩いていると、公園の木の陰に巨大な影が見えた。僕は立ち止まってしまい、すぐにそれがこちらの方を向く。陰陽師の真っ白な紙人形に目と口を適当に書いたような風貌をしている。それは、虫取り網のようなものをもっている。\n　僕は直感でそれが危ないものだと感じた。走って逃げようにも思うように動かない。少しずつ後ろに下がるが、その歩幅に合わせるように迫ってくる。よく見ると、近くにあった木やベンチがなくなっている。網に吸い込まれている？\n　やっとの思いで後ろを見ると、もう一体近づいてくる。なんとかして横に逃げると、それらはなぜかまだ直進する。訳が分からないが、二つが互いに近づくにつれて吸引力が強くなっていく。どうなるのかと見守っていると、二つはぶつかり合い、お互いの網に吸い込まれていった。残った網はくっついて、最終的には対消滅した。\n　何だったんだ、あれは。",
      },
      {
        id: "destination",
        title: "行先未定",
        text: "　バスの終着点は、落ち着いた色の道の駅だった。途中で見た一面の緑と比較すれば、人が居る場所に降り立ったことを喜ぶべきか。または、この道の駅以外に観光客向けの施設がなさそうなことを悲しむべきか。それが目的地のない一人旅の楽しさではあるけれど。\n　売店でラムネを一つ買った。いつもの旅のお供、飲み干せば夏の化身。カラカラと鳴る球が暑さを和らげる。道の駅から出て道なりに行く中で、周りからは色々な音が聞こえてくる。右手には海、左手には山、上には青空、近くには田んぼ。おおよそ二色のこれらの組み合わせも随分見てきたはずなのに未だに飽きないように思えるのは、きっとこの色の組み合わせが好きだからだろう。\n　海沿いの住宅街をぶらぶら歩いていると、家々の間から覗いていた海に続く道を見つけた。歩いていくと、舗装されていたのは初めの少しだけで、それからはずっと草を踏み鳴らしただけの道が続く。歩く度に、草が擦れる音や、波が押し寄せる音がする。風走る草原の中に立っている気もしたが、生憎潮の匂いがする。\n　浜に着くと、そこには白いテントと机、海には一部を切り取るようにブイが弧を描いて浮いていた。恐らくその区域内だけは泳げるのだろう。当然水着などは持ってきていないし、サンダルもないので、履きなれたスニーカーで濡れない程度に近づいて潮風を浴びた。寄っては消えていく波と音で時間が過ぎていく。\n　机に座りながらボーっとしていると、ふと行きしなの風景を思い出した。上下に分かれた二色が青と緑でなくとも心地よさを覚えるのなら、真に私が好きなのは色ではないのか。青い色が夕暮れの赤でも、曇天の灰でも、深夜の紫でもいいのだろう。それこそ、二色だけでなく、三色、四色、きっと空に色相環が燦々と輝くようなときにも思うというのは過言か。時折変に話が逸れて戻っても、私が心地よさを感じているものが何なのかは一向に見えてこない。\n　それでも時間は経ってしまうから、仕方なく写真に収めて他の場所へ行こうとする。さっきとは逆方向の音と風が新鮮さを与え、余計に方向音痴を加速させる。右へ行こうか左へ行こうか、気の向くままに足を運べるのも一人旅の楽しさだ。といっても、宿のことは多少気にかけないといけないけれど。あともう少しの時間の使い方を考えながら、適当に左に曲がった。ラムネ瓶がカランと鳴った。夏の夕方。",
