      {
        id: "toriyasai",
        title: "すいキッチン　とり野菜",
        text: "　今年、大学生になった。私は地元から遠く離れて、一人暮らしの自炊生活が始まった。\n　四月からは全国から集まった同級生や大学の先輩たちと、なんやかんや楽しいキャンパスライフが待っていると思っていたけれども、実際に待っていたのはオンライン授業。いいことも悪いこともありつつ、なんとかいろいろ飢えを凌いでいる。\n　とはいえ、四月初めは私はそこまで緊迫感なく、とりあえず外出自粛しておこうかと軽い気持ちでいた。残念だったのは、自己紹介用に用意していた地元ネタが一切使えなくなることだった。私は地元愛がすごいと自負していて、軽い知識くらいは披露できるけれど、やはりインパクトがあるのは実物だろうということで、いざとなれば地元でしか売っていないようなものを大学に持っていって布教する覚悟でいた。\n　その地元のローカル品で私が特にお気に入りだったのは、まつやのとり野菜みそだった。とり野菜みそというのは、とり野菜と言われるめちゃくちゃ美味しいみそ鍋の素のことだ。オンライン販売をしているらしいので、気になったら買ってみてほしい。新しくできる友人を自宅に招いて、これを振舞おうと目論んでいたが、残念ながら未遂に終わってしまった。こればかりはコロナが憎い。来年に持ち越すのもアリだなと思っていたが、賞味期限を見ると今年の夏まで。どっちにしろ持ってきたこのみそは食べきらなければいけない。暑がりというのもあって、夏に鍋は自殺行為。仕方なく鍋を作った。\n　一人暮らしの自炊は、まず材料を買うところから始まる。白菜は必須。あとは家庭によって違うと思うが、今回は豚肉細切れ、えのきたけ、ニンジン、木綿豆腐。しらたきとか入れてもたぶん美味しい。\n　それでは、食材を調理していこう。豆腐一丁を手の上に乗せて水切り。包丁を横にスッと入れて半分に切ったあと、上から包丁を入れて九等分、そして小鍋に投入。\n　白菜は数枚、包丁で食べやすいサイズに切っていく。包丁を使うのにまだ慣れていないので、途中面倒になって葉先の柔らかい部分は手でちぎった。最終的においしく楽しく食べられれば大丈夫。\n　えのきたけは石づきの部分は捨ててしまった。食後に調べたら、あそこは食べられるらしい。ちゃんとほぐして、食べきれそうな量を投入。残りの部分もほぐしておくと後々いい。ただ全部まとめて冷凍すると次食べるときに難儀するので小分けにしておこう(二敗)。\n　ニンジンは二分の一本をいちょう切り。正直、一人分でこれは入れすぎかもしれない。だけど美味しかったので大丈夫。ちょっと分厚くなった気がしたら、小鍋に入れる前に電子レンジで二、三分チンしておくと中まで柔らかくなる。まあ長時間煮込めばいいだけなんだけど、そこまで待てなかった。\n　豚肉はもう切ってあるだろうと思ってそのまま投入。これで材料は全部入れたかな。\n　そしたら次はいよいよみその出番。とり野菜みそ一袋二〇〇グラムの半分を鍋に入れる。これで二、三食分作れる。一日は余裕でもつので胃腸の心配はいらない。むしろ万病に効くかもしれない。そして水は五〇〇シーシーくらい入れる。私の小鍋だと、具が多ければこれでいっぱいになる。あとはみそをよく溶かして中火で煮る。ふたをするのは忘れずに。\n　焦げたらいろいろまずいので、定期的にかき混ぜる。全然待てなかったので一分ごとに混ぜてたけど、さすがにそこまではいらないと思う。心に余裕を持ってほかのことをしよう。混ぜるのが強すぎると豆腐が崩れるので適度な速さで。沸騰しかかったら、弱火かとろ火にして十分から十五分ほど煮る。これだけ時間かければニンジンも柔らかくなっているはず。豚肉は細切れなら充分火が通っているので大丈夫。\n　できた頃合いを見計らってふたを開けると、湯気が一気に出てくる。これは美味しさの証明、確定演出。湯気が晴れたら、みそのベージュ色が見えてくる。表面に豚肉の脂が浮いているかもしれないが、それは非常にうまみが詰め込まれているので、野菜と一緒に食べるといい。\n　ようやく実食に移る。器に具を盛り、スープを上からざっとかける。湯気が多ければ、はふはふして冷まそう。そうすると涎の準備ができる。食べごろになると、具がつややかに目に映る。見て分かる美味しさ。圧倒的ＱОＬの上昇。戦う前にして優勝を確信する。具を一つつまんで口に入れると、具本来のうまさと、みその風味が同時に殴りかかってくる。スープ単体の威力もものすごいので、ぜひ単独でも賞味してほしい。野菜と肉とキノコのエキスがみそと合わさって、とても濃厚である。いちいちパンチを食らっていると最後の一口まで耐えられなくなるので、ぬるくなる前に食べてしまおう。それでも満腹が来てしまい、鍋に半分ほど残るはず。あとは明日。\n　次の日。白米を炊く。もちろん食べきれる量。炊けたらそれを冷めた鍋に入れる。冷めたままでいい。どうせ温める。ちょうどいい温度になったら食べよう。二回煮ると確実に、さらに具が柔らかくなる。スープがお米に染みて二度美味しい。うまみの再来。最後の一口まで、スープを味わっていただく。\n　ごちそうさまでした。満足感は暖かくなった身体と共に。",
